import DataServices from "@/services/DataService";
let buttons =
    [
        {
            title: 'Add',
            iconClass: 'fa fa-plus',        
            callbackfunction: 'callAdd',
            additionalClass: '',
            isdisabled: false,
            isvisible: true,
            checkPrivilege: true,
            id:'AddPolicy',
             
        }
    ];

let leftsearchSchema = [
    {
        fieldName: 'Category',
        fieldType: "radio",
        fieldIcon: "fa fa-envelope",
        fieldSearchConditionName: [''],
        paramName: "categoryId",
        isSearch: false,
        value: "",
        listOptions: []

    },
    {
        fieldName: 'Search By Keyword',
        fieldType: "text",
        fieldIcon: "fa fa-envelope",
        fieldSearchConditionName: [''],
        paramName: "search",
        isSearch: false,
        value: "",
        listOptions: []

    }
];
async function bindleftCommonSearchdropdown(instance,modulename, filtername) {    
    var vm = this;   
    //debugger;
    if(filtername=='Category'){        
        DataServices.GetCategoryDDL().then(response => {
          //  debugger
            if (response.data.result.length > 0) {
                var CategortData = response.data.result;
                vm.leftsearchSchema[0].listOptions =[];
                CategortData.forEach(element => {
                    if (element.TICKET_ARTICLE_CATEGORY_PARENT == null) {
                        let obj = {
                            name: element.TICKET_ARTICLE_CATEGORY_NAME,
                            value: element.TICKET_ARTICLE_CATEGORY_ID
                        }                           
                        vm.leftsearchSchema[0].listOptions.push(obj);
                    }
                    // if (element.TICKET_ARTICLE_CATEGORY_PARENT == element.TICKET_ARTICLE_CATEGORY_PARENT) {
                    //     let obj = {
                    //         name: element.TICKET_ARTICLE_CATEGORY_NAME,
                    //         value: element.TICKET_ARTICLE_CATEGORY_ID
                    //     }                            
                    //     vm.leftsearchSchema[0].listOptions.push(obj);
                    // }
                });
            }
        })
    }
       
} 

export default { buttons, leftsearchSchema ,bindleftCommonSearchdropdown};