<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" ModuleName="Ticketing" SubModuleCode="CANNED_REPLY"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="ArticleId"
            :HeaderText="$t('Header_KB')" :ListData="SAData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchData" :showCheckBox="false" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">    
            <template v-slot:slotdata="{data}">              
                <template v-if="data.column.COLUMN_NAME == 'View'">
                    <td class="text-center single-action" style="">
                        <span><em class="fa fa-file-text-o" aria-hidden="true"></em><span class="noti-circle noti-green">{{ data.row.NoOfViews }}</span></span>
                    </td>                   
                </template>
                <template v-if="data.column.COLUMN_NAME == 'CreatedDate'">
                    <td class="text-center single-action tourguide_a">
                       {{ $options.filters.formatDate(data.row.CreatedDate) }}
                    </td>                    
                </template>
            </template>        
        </tg-list>        
    </section>
</template>
<script>
import Schema from './IndexSchema'
import DataServices from "@/services/DataService";
export default {   
    data() {
        return {
            isLoading: false,
            IsAddCannedReply: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,           
            Headers: [{
                "COLUMN_NAME": "ArticleName",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Title",
                "DISPLAY_ORDER": 1 
                }, 
                {
                "COLUMN_NAME": "Author",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Author",
                "DISPLAY_ORDER": 2,
                 },
                 {
                "COLUMN_NAME": "CategoryName",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Category",
                "DISPLAY_ORDER": 2,
                 },
                 {
                "COLUMN_NAME": "View",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "View",
                "DISPLAY_ORDER": 2,
                settings : {
                    isInSlot : true
                 }
                 },
                 {
                "COLUMN_NAME": "CreatedDate",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Date",
                "DISPLAY_ORDER": 2,
                settings : {
                    isInSlot : true
                 }
                 
                 }
                ],
            SAData: [],
            SortBy: '',
            SortExp: '',
            TotalRecords: 0,
            PageNumber:1,
            PageSize:10,
            searchCondition:'',
            TemplateId:null
        }
    },
    created:async function () {
        await Schema.bindleftCommonSearchdropdown(this, "Ticketing", "Category");
        this.FetchData();
    },
    methods:{
        FetchData:function(){              
            var vm=this;
            vm.isLoading=true;
            var url = `sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&${vm.searchCondition}`;
            DataServices.GetKnowledgeBaseList(url).then(response=>{
              //  debugger                                                
                if(response.data.status=='Success'){    
                    if(response.data.result.data.length>0){
                        var ArticleCategory = [];
                        var groupedCategories = {}; 
                        vm.SAData =[];
                        response.data.result.data.forEach(function (info) {
                            var key = `${info.TICKET_ARTICLE_CATEGORY_NAME}`;
                            if (!groupedCategories[key]) {
                                groupedCategories[key] = {
                                    CategoryName: info.TICKET_ARTICLE_CATEGORY_NAME,
                                    ArticleCount: 0,
                                    categoryId: info.TICKET_ARTICLE_CATEGORY_ID,
                                    ArticlesList: []
                                };
                            }
                            groupedCategories[key].ArticleCount++;                            
                            groupedCategories[key].ArticlesList.push({                                
                                categoryId: info.TICKET_ARTICLE_CATEGORY_ID,
                                CategoryName: info.TICKET_ARTICLE_CATEGORY_NAME,
                                ArticleId: info.ART[0].TICKET_ARTICLE_ID,
                                ArticleName: info.ART[0].TICKET_ARTICLE_TITLE,
                                ArticleData: info.ART[0].TICKET_ARTICLE_DESC,
                                NoOfViews: info.ART[0].NO_OF_VIEWS,
                                KeyWords: info.ART[0].TICKET_ARTICLE_KEYWORDS,
                                Author: info.ART[0].Author,
                                CreatedDate: info.ART[0].CREATED_AT
                            });
                        });
                        
                        for (var key in groupedCategories) {
                            ArticleCategory.push(groupedCategories[key]);
                        }                        
                        ArticleCategory.forEach(function (item) {                            
                            item.ArticlesList.forEach(function(val){
                                vm.SAData.push(val)
                            })
                        })
                    }   
                    //debugger   
                    vm.isLoading=false;              
                    vm.TotalRecords = vm.SAData.length;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;                    
                }
            })
        },
        commonHeaderButtonClick: function (event) {            
            switch (event.callbackfunction) {
                case 'callAdd':
                    this.CreateArticle();
                    break;
            }
        },
        CreateArticle:function(){
            this.$router.push({ name: 'CreateArticle',params: { id: 0 } });
        },
        renderActions: function () { },
        //listheaderbuttons: function () { },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {  
            //debugger;             
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchConditionJson != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        actionButtonClick: function () { },
        sortdata: function (sortBy) {            
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        openaddCannedReply:function(){ 
            this.TemplateId=this.TemplateId;           
            this.IsAddCannedReply=true;
        },
        EditCannedReply:function(ths){
            this.TemplateId=ths;
            this.IsAddCannedReply=true;
        },
        UpdateStatus: async function (status) {            
            var vm = this;
            if (status == 'Active') {
                status = 1001
            }
            else {
                status = 1002
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmUpdateStatusRecord"), true, (vm.$t("Active") + "  " + vm.$t("Inactive")), function () {
                    var url = `id=${val}&statusId=${status}&isFor=${'ReplyTemplate'}`;
                    DataServices.updatestatus(url).then(response => {                        
                        if (response.data == true) {
                            vm.listheaderbuttons[1].isdisabled=true;
                            vm.listheaderbuttons[2].isdisabled=true;
                            vm.listheaderbuttons[3].isdisabled=true;
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    })
                })
            }
        },
        
    }    
}
</script>
<style>
.textstyle{
    color: #007bff;
    text-decoration: none;
    background-color: transparent;
}
</style>